<template>
	<section class="pro_det">
		<!-- 添加新商品 -->
		<table class="add_tab">
			<tr>
				<td colspan="2">
					<h2>添加商品</h2>
				</td>	
			</tr>
			<tr>
				<td class="name">商品名称</td>
				<td>
					<input type="text"  id ="adName" >
				</td>
			</tr>
			<tr>
				<td class="price">商品价格</td>
				<td>
					<input type="number"  id="ad_price">
				</td>
			</tr>
			<tr>
				<td>商品分类</td>
				<td>
					<select class="classtype">
						<option v-for="type in typeList" :value="type.id">{{type.text}}</option>
					</select>
				</td>
			</tr>
			<tr>
				<td class="attr">商品属性</td>
				<td>
					<input type="text" name="" v-show="isAddAttrInput" v-model="newAttrVal">
					<button class="addattr fin-search" @click="isAddAttrInput?addAttr(newAttrVal):isAddAttrInput=true">{{isAddAttrInput?'完成':'添加属性'}}</button>
					<div class="item_attr" v-for="(attr,index) in attrList" >
						<span class="attr_name" @click="attr.isShowList=!attr.isShowList">
							{{attr.name}}
							<span>↓</span>
							<div class="attr_value_list" v-show="attr.isShowList">
								<div v-for="attrValue in attr.valueList">
									{{attrValue.name}}
								</div>
							</div>
						</span>
						<button class="fin-delete" @click="deleteOneAttr(index)">删除</button>
						<button class="fin-search" @click="attr.isShowInput?addAttrValue(attr):attr.isShowInput=attr.isShowList=true" >{{attr.isShowInput?'完成':'添加属性值'}}</button>
						<input type="text" name="" v-show="attr.isShowInput" v-model="attr.inputVal">
					</div>
				</td>
			</tr>
			<tr>
				<td>商品库存</td>
				<td class="add_number">
					<div v-for="(oneStock,key) in stockList">
						库存：<input class="attr_value_num" type="number" name="" v-model="oneStock.number">
						<span v-for="oneVal in oneStock.attrValues">&nbsp;&nbsp;{{oneVal}}&nbsp;&nbsp;&nbsp;&nbsp;</span>
					</div>
				</td>
			</tr>
			<tr>
				<td class="status">商品状态</td>
				<td>
					<select id="ad_status" >
						<option>上架</option>
						<option>下架 </option>
					</select>
				</td>
			</tr>
			<tr>
				<td>商品推荐</td>
				<td>
					<select id="ad_product" >
						<option>推荐</option>
						<option>不推荐</option>
					</select>
				</td>
			</tr>
			<tr>
				<td class="images">商品图片</td>
				<td>
					<div class="img_show" v-for="(imgSrc,index) in imageSrcList">
						<img class="img1" :src="imgSrc">
						<button class="fin-delete" @click="deleteImg(index)">删除</button>
						<!-- <span>加载中..</span> -->
					</div>
					<div>
						<input type="file" id="ad_images" @change="uploadImgUrl($event)">
					</div>
				</td>
			</tr>
			<tr>
				<td class="introduce">商品介绍</td>
				<td>
					<textarea id="ad_introduce" ></textarea>
				</td>
			</tr>
			<tr>
				<td>商品详情内容</td>
				<td>
					<vue-html5-editor id='aa' :content="content" :height="200"  ref="editor"></vue-html5-editor>
				</td>
			</tr>
			<tr>
			    <td></td>
				<td>
					<div>
				       <button class="all_back fin-search" @click="backClick">返回</button>
				       <button class="add_ok fin-search" @click="addOk">提交</button>
			       </div>
				</td>
			</tr>
			
		</table>

	</section>
</template>

<script>
import ser from "../js/serverUrl";
import Vue from 'vue';
import VueHtml5Editor from 'vue-html5-editor';
	Vue.use(VueHtml5Editor, {
	    // 全局组件名称，使用new VueHtml5Editor(options)时该选项无效 
	    // global component name
	    name: "vue-html5-editor",
	    // 是否显示模块名称，开启的话会在工具栏的图标后台直接显示名称
	    // if set true,will append module name to toolbar after icon
	    showModuleName: false,
	    // 自定义各个图标的class，默认使用的是font-awesome提供的图标
	    // custom icon class of built-in modules,default using font-awesome
	    icons: {
	        text: "fa fa-pencil",
	        color: "fa fa-paint-brush",
	        font: "fa fa-font",
	        align: "fa fa-align-justify",
	        list: "fa fa-list",
	        link: "fa fa-chain",
	        unlink: "fa fa-chain-broken",
	        tabulation: "fa fa-table",
	        image: "fa fa-file-image-o",
	        hr: "fa fa-minus",
	        eraser: "fa fa-eraser",
	        undo: "fa-undo fa",
	        "full-screen": "fa fa-arrows-alt",
	        info: "fa fa-info",
	    },
	    // 配置图片模块
	    // config image modulesss
	    image: {
	        // 后端图片上传的地址，如果为空，默认转图片为base64
	        // Url of the server-side,default null and convert image to base64
	        server: null,
	        // 请求时表单参数名
	        // the name for file field in multipart request
	        fieldName: "image",
	        // 文件最大体积，单位字节  max file size
	        sizeLimit: 512 * 1024,
	        // 是否压缩，默认true，设置为true时会使用localResizeIMG进行压缩
	        // default true,if set to true,the image will resize by localResizeIMG (https://github.com/think2011/localResizeIMG)
	        // compress: true,
	        // 图片压缩选项
	        // follows are options of localResizeIMG
	        // width: 960,
	        // height: 450,
	        // quality: 100,
	        // 响应数据处理
	        // handle response data，return image url
	        uploadHandler(responseText){
	            //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
	            var json = JSON.parse(responseText)
	            if (!json.ok) {
	                alert(json.msg)
	            } else {
	                return json.data
	            }
	        }
	    },
	    // 语言，内建的有英文（en-us）和中文（zh-cn）
	    //default en-us, en-us and zh-cn are built-in
	    language: "zh-cn",
	    // 自定义语言
	    i18n: {
	        //specify your language here
	        "zh-cn": {
	            "align": "对齐方式",
	            "image": "图片",
	            "list": "列表",
	            "link": "链接",
	            "unlink": "去除链接",
	            "table": "表格",
	            "font": "文字",
	            "full screen": "全屏",
	            "text": "排版",
	            "eraser": "格式清除",
	            "info": "关于",
	            "color": "颜色",
	            "please enter a url": "请输入地址",
	            "create link": "创建链接",
	            "bold": "加粗",
	            "italic": "倾斜",
	            "underline": "下划线",
	            "strike through": "删除线",
	            "subscript": "上标",
	            "superscript": "下标",
	            "heading": "标题",
	            "font name": "字体",
	            "font size": "文字大小",
	            "left justify": "左对齐",
	            "center justify": "居中",
	            "right justify": "右对齐",
	            "ordered list": "有序列表",
	            "unordered list": "无序列表",
	            "fore color": "前景色",
	            "background color": "背景色",
	            "row count": "行数",
	            "column count": "列数",
	            "save": "确定",
	            "upload": "上传",
	            "progress": "进度",
	            "unknown": "未知",
	            "please wait": "请稍等",
	            "error": "错误",
	            "abort": "中断",
	            "reset": "重置"
	        }
	    },
	    // 【隐藏】不想要显示出来的模块
	    // the modules you don't want
	    hiddenModules: [],
	    // 自定义要【显示】的模块，并控制顺序
	    // keep only the modules you want and customize the order.
	    // can be used with hiddenModules together
	    visibleModules: [
	        "text",
	        "color",
	        "font",
	        "align",
	        "list",
	        "link",
	        "unlink",
	        "tabulation",
	        "image",
	        "hr",
	        "eraser",
	        "undo",
	        "full-screen",
	        "info",
	    ],
	    // 扩展模块，具体可以参考examples或查看源码
	    // extended modules
	    modules: {
	        //omit,reference to source code of build-in modules
	    }
	})
	
	var vm = {
		data(){
			return {
				isAddAttrInput:false,
				isAttrValueInput:false,
				isAttrValueList:false,
				attrList:[],
				newAttrVal:"",
				dataList:[],
				imageSrcList:[],
				stockList:{},
				adName: '',
				typeList:[],
				content:''
			}
		},
		methods:{
			backClick:function(){
				this.$router.push({ path: 'productDetails', query: {}})
			},
			addproduct:function(){
				
				$('.pro_det .add_tab input').val("");
				$('.pro_det .add_tab div').val("");
				$('.pro_det .add_tab textarea').val("");
			},
            // updateData: function (data) {
            //     // sync content to component
            //     this.content = data
            // },
			addOk:function(){
				if(confirm("确定添加该商品？")){
					var _this=this;
					function concatAttr(){
						var arr = [];
						for(var i=0;i<_this.attrList.length;i++){
							arr.push({name:_this.attrList[i].name,valueList:_this.attrList[i].valueList});
						}
						return arr;
					}
					function concatAttrValue(){
						var arr=[];
						for(var key in _this.stockList){
							arr.push(_this.stockList[key]);
						}
						return arr;
					}
					var tname = $('.pro_det #adName').val()
                    var tprice = $('.pro_det #ad_price').val()
                    var tintroduce = $('.pro_det #ad_introduce').val()
                    // var tcontent = _this.content
                    // var tcontent=_this.$refs.editor;
                    var tcontent=$('.content').html()
                    // var tcontent = $('.pro_det #ad_content').val()
                    var tnumber = concatAttrValue()
                    var timages = this.imageSrcList
                    var tattr = concatAttr()
					var addarr = {
						name:tname,
                        price:tprice,
						attr:tattr,
						number:tnumber,
						status:$('.pro_det #ad_status').val(),
						images:timages,
						introduce:tintroduce,
						produce:$('.pro_det #ad_product').val(),
						content:tcontent,
						cid:$('.classtype option:selected').val()
					}
					console.log(_this.content)
					if(tname !== "" && tprice !== "" && tintroduce !== "" && tcontent !== "" && tnumber.length>0 && timages.length>0 && tattr.length>0){

						$.ajax({
							type:'POST',
							url :ser.url("addGoods"),
							data:addarr,
							dataType: 'json',
							success:function(resp){
								if (resp.code == 200) {
									console.log(resp);
									_this.util.alertMsg(resp.msg)	
									// window.location.href="/option.html#/productDetails"
									_this.$router.push({ path: 'productDetails', query: {}});
								} else {}
							}
						})
					}else{
						_this.util.alertMsg("内容不能为空")
					}
				}
			},
			deleteImg:function(index){
				this.imageSrcList.splice(index,1);
			},
			uploadImgUrl:function(event){
				var formdata = new FormData();
				
				formdata.append("imgFile", event.currentTarget.files[0]);
				var _this=this;
				$.ajax({
					type:'post',
					url :ser.url("doUploadImgGoods"),
					data : formdata, 
					dataType: 'json',
					cache : false, 
					contentType : false, 
					processData : false, 
					config: {
						headers: {'Content-Type': 'multipart/form-data'}
					},
					success:function(resp){
						_this.imageSrcList.push(resp.data); 
	              }
	          })
				return false;  
			},
			deleteOneAttr:function(index){
				var oneData = this.attrList.splice(index,1);
				this.stockList = {};
				this.concatStock();
			},
			addAttr:function(newAttr){
				this.isAddAttrInput = false;
				if(newAttr == ""){
					return;
				}
				this.attrList.push(
				{
					name:newAttr,
					isShowInput:false,
					isShowList:false,
					inputVal:"",
					valueList:[]
				}
				);
				this.newAttrVal = "";
			},
			concatStock:function(){
				var _this=this;
				function countNum(index,zuohekey){

					var list = _this.attrList[index].valueList;
					for(var i=0;i<list.length;i++){
						//如果不存在进行添加

						if(!_this.stockList[zuohekey+list[i].name]){
							_this.stockList[zuohekey+list[i].name]=zuohekey==""?
							{number:0,attrValues:[list[i].name]}:
							{number:0,attrValues:_this.stockList[zuohekey].attrValues.concat(list[i].name)}
						}
						//最后一轮时辅助数据删除，前几轮的话还需要使用
						//例如[aabbcc][aabbdd]，那么aabb则是辅助数据，在最后dd时删除
						if(i == list.length-1){
							delete _this.stockList[zuohekey];
						}
						if( index < _this.attrList.length-1){
							countNum(index+1,zuohekey+list[i].name)
						}
					}
				}
				if(this.attrList.length>0){
					countNum(0,"");
				}
			},
			addAttrValue:function(attr){
				if(attr.inputVal != ""){
					attr.valueList.push({name:attr.inputVal});
					attr.inputVal = "";
				}
				this.concatStock();
				attr.isShowInput=false;
			},
			loadData:function(data){
				var _this=this
				$.ajax({
					type: "POST",
					url :ser.url("getGoodsList"),
					dataType : "json",
					success : function(resp){
						_this.dataList=resp.data;	
					}
				})	
			},
			produtType:function(){
				var _this = this;
				$.ajax({
					type:'POST',
					url :ser.url("getClassifyList"),
					dataType: 'json',
					success:function(resp){
						_this.typeList=resp.data;
					}
				})
			}
		},
		mounted(){
			 this.produtType();
		}
	};
	export default vm;
</script>

<style scoped lang="scss">
    *{
		font-family: '微软雅黑';
	}
	table{
		border:1px solid #ddd;
		background-color:#fff;
	}
	button{
		font-size: 12px;
		padding: 5px;
		margin: 5px auto;
	}
	// .content{
	// 	width:300px;
	// 	height:200px;
	// }
	.addproduct{
		width:80px;
		margin: 20px auto;
		right:200px;
	}
	
	.add_tab{
		width: 100%;
		text-align:left;
		line-height: 58px;
		tr{	
			td{
				.item_attr{
					button{
						margin-left: 10px;
					}
				}
				textarea{
					margin-top:20px;
					width:500px;
					height:200px;
				}
				.attr_name{
					border: 1px solid #aaa;
					vertical-align: middle;
					padding: 5px;
					position: relative;
					.attr_value_list{
						min-width: 100%;
						box-sizing: content-box;
						position: absolute;
						top: 100%;
						margin: -1px;
						left: 0;
						border: 1px solid #aaa;
						background-color: #ffffc8;
						z-index: 1000;
						>div{
							white-space: nowrap;
						}
					}

				}
				.attr_value_num{
					width:100px;
					text-align: center;
				}
				*{
					vertical-align: middle;
				}
				.add_ok{
					width: 77px;
                    font-size: 14px;
				}
				.all_back{
					width: 77px;
                    font-size: 14px;
				}
				.fin-delete{
					width:45px;
				}

			}
			td:nth-child(1){
				text-align:center;
				vertical-align: top;
			}
			
		}
		#ad_introduce{
			width: 500px;
			height: 200px;
		}
		.img_show{
			display:inline-block;
			width:120px;
			text-align: center;
			img{
				width:70px;
				height:70px;
				border:none;
			}
			span{
				display:block;
				font-size:15px;
			}
		}
	}
</style>


