<template>
	<section class="order-manage">
		<button class="back" v-show="elDisplay1" @click="elDisplay=!elDisplay,elDisplay1=!elDisplay1">订单列表</button>
		<table class="banner_infor" v-show="!elDisplay">
			<tr>
				<td class="sort" @click="util.alert('oooo')">用户id</td>
				<td class="id" >订单id</td>
				<td class="title" > 订单编号</td>
				<td class="image" >
					<select class="" name="" id="" @click="select()">
						<option value="">订单状态</option>
						<option value="">已发货</option>
						<option value="">待发货</option>
						<option value="">已取消</option>
					</select>
				</td>
				<td class="type" >支付状态</td>
				<td class="text" >支付方式类型</td>
				<td class="link" @click="doSort()">创建时间</td>
				<td class="createtime" >订单总价</td>
				<td class="set" >订单详情</td>
			</tr>
			<tr v-for="data in computData()">
				<td class="id">{{data.user_id}}</td>
				<td class="" >{{data.id}}</td>
				<td class="" >{{data.order_sn}}</td>
				<td class="">{{data.order_status}}</td>
				<td class="" ><input class="pay_status" type="text" v-model="data.pay_status" disabled="ture"></td>
				<td class="" >{{data.pay_type}}</td>
				<td class="createtime" >{{data.createtime}}</td>
				<td class="sort" >{{data.order_total}}</td>
				<td class="set" >
					<button class="look" @click="change(data),elDisplay=!elDisplay,elDisplay1=!elDisplay1">查看</button>
				</td>
			</tr>
			<tr>
				<td colspan="6" id="fenye"></td>
			</tr>
			
		</table>
		<table class="banner_infor" v-if="elDisplay1">
			<tr>
				<td class="" >订单id</td>
				<td class="" >商品单价</td>
				<td class="" >商品数量</td>
				<td class="" >订单总价</td>
				<td class="" >支付状态</td>
				<td class="" >订单状态</td>
				<td class="" >地址id</td>
				<td class="" >修改订单信息</td>

			</tr>
			<tr>
				<td class="" >{{curData.id}}</td>
				<td class="">{{curData.good_price}}</td>
				<td class="" >{{curData.good_number}}</td>
				<td class="" >{{curData.order_total}}</td>
				<td class="" >{{curData.pay_status}}</td>
				<td class="id">{{curData.order_status}}</td>
				<td class="sort" >{{curData.address_id}}</td>
				<td class="set" >
					<button class="wirte-btn" @click="elDisplay2=!elDisplay2">修改订单</button>
				</td>
			</tr>
			<tr class="wirte-order" v-show="elDisplay2">
				<td><input type="text" placeholder="输入修改后的id" disabled="false"></td>
				<td><input type="text" placeholder="输入修改后的单价" disabled="false"></td>
				<td><input type="text" placeholder="输入修改后的数量" disabled="false"></td>
				<td><input type="text" placeholder="不可修改" disabled="flase"></td>
				<td><input type="text" placeholder="不可修改" disabled="flase"></td>
				<td>
					<select class="sele" name="" id="">
						<option>已发货</option>
						<option>待发货</option>
						<option>已取消</option>
					</select>
				</td>
				<td><input type="text" placeholder="输入修改后的地址"></td>
				<td><button class="write-ok" @click="elDisplay2=!elDisplay2,writeOrder(curData)">完成</button></td>
			</tr>
			<tr v-show="elDisplay2">
				<td colspan="8">物流信息</td>
			</tr>
			<tr  v-show="elDisplay2" class="wlmsg">
				<td colspan="4"><input class="expressName" type="text" placeholder="填写物流公司名称"></td>
				<td colspan="4"><input class="expressNumber" type="text" placeholder="填写快递单号"></td>
			</tr>

		</table>
	</section>
</template>

<script>
import ser from "../js/serverUrl";
	export default {
		data(){
			return {
				elDisplay:false,
				elDisplay1:false,
				elDisplay2:false,
				dataList:[],
				curData:null,
				filter:null,
				currentPage:'1',       //默认 当前第一页
				allPage:'',
				data11:{
					num:'10',
					currentPage:'1'
				}
			}
		},	
		methods:{
			writeOrder:function(curData){
				var data1 = {
					order_status:$('.order-manage .sele').find("option:selected").text(),
					expressName:$('.order-manage .expressName').val(),
					expressNumber:$('.order-manage .expressNumber').val(),
					id:curData.id
				}
				console.log(data1.order_status + data1.id);
				var _this = this;
				$.ajax({
					type:'POST',
					url :ser.url("modifyOrderStatus"),
					data:data1,
					dataType: 'json',
					success:function(resp){
						if (resp.code==200) {
							alert(resp.msg);
							_this.loadData();
						}else{
							alert(resp.msg);
							_this.loadData();
						}
					}
				})
			},
			//定义计算
			computData:function(){
				if(this.filter){
					var _this = this;
					return this.dataList.filter(function(data){
						return data.order_status == _this.filter;
					});
				}else{
					return this.dataList;
				}
			},
			select:function(){
				this.filter = $('.image select').find(":selected").text();
				if(this.filter == '订单状态'){
					this.filter = null;
				}
			},
			doSort:function(){
				if (this.flag) {
					function sort(a,b){
						return  b.createtime -a.createtime;
					}
					this.dataList.sort(sort);
					this.flag = false;
				}
				else{
						function sort(a,b){
						return a.createtime - b.createtime;
					}
					this.dataList.sort(sort);
					this.flag = true;
				}
			},
			// 单挑商品
			change:function(data){
				this.curData=data;
				// console.log(data)
			},
			// 进入页面自动加载
			loadData : function(){
				
				var _this=this
				var ul=document.createElement('ul');
				var li_first=document.createElement('li');
				var li_pre=document.createElement('li');
				var li_next=document.createElement('li');
				var li_last=document.createElement('li');
				var li_nowPage=document.createElement('li');
				var li_allPage=document.createElement('li');

				li_first.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer; color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_pre.setAttribute('style', 'background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_next.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				
				li_last.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')


				li_first.setAttribute('id','li_first')
				li_pre.setAttribute('id','li_pre')
				li_next.setAttribute('id','li_next')
				li_last.setAttribute('id','li_last')
				li_nowPage.setAttribute('id','li_nowPage')
				li_allPage.setAttribute('id','li_allPage')

				ul.appendChild(li_first);
				ul.appendChild(li_pre);
				ul.appendChild(li_next);
				ul.appendChild(li_last);
				ul.appendChild(li_nowPage);
				ul.appendChild(li_allPage);

				li_first.innerHTML='首页';
				li_pre.innerHTML  ='上一页';
				li_next.innerHTML ='下一页';				
				li_last.innerHTML ='尾页';
				
				function dd(a){
					a.onmouseover=function(){
						this.style.background='#333';
						this.style.color='#fff';
					}
					a.onmouseout=function(){
						this.style.background='#ddd';
						this.style.color='#000';
					}
				}
				dd(li_first)
				dd(li_next)
				dd(li_pre)
				dd(li_last)
				
				document.getElementById('fenye').appendChild(ul);
				this.fajax();
			},
			fajax:function(){
				var _this=this
				/*var allPage;*/
				console.log(_this.data11)
				$.ajax({
					type: "POST",
					url :ser.url("getMSOrderList"),
					data:_this.data11,
					dataType: 'json',
					// dataType : "json",
					success : function(resp){
						if(resp.acount){
			        		_this.allPage=Math.ceil(resp.acount/_this.data11.num);
			        	}
			        	_this.doclick()
							_this.dataList=d.data;
						li_nowPage.innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';	
					}
				})

			},
			doclick:function(){
				var _this = this
				li_first.onclick=function(){
					if(_this.data11.currentPage!=1){
						_this.data11.currentPage=1;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';
					console.log(_this.data11.currentPage)
				}

				li_pre.onclick=function(){		
					if(_this.data11.currentPage<=_this.allPage&&_this.data11.currentPage>1)
					{
						_this.data11.currentPage--;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
				li_next.onclick=function(){
					if(_this.data11.currentPage<_this.allPage&&_this.data11.currentPage>=1)
					{
						_this.data11.currentPage++;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}

				li_last.onclick=function(){
					if(_this.data11.currentPage!=_this.allPage){
						_this.data11.currentPage=_this.allPage;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
			},
		},
		mounted(){
				this.loadData();
		}
	}
</script>

<style scoped lang="scss">
	*{
		font-family: '微软雅黑';
	}
	.back{
		width: 100px;
		border: solid 1px #398DEE;
		color: #398DEE;
		font-size: 14px;
		background-color: #fff;
	}
	 button{
	 	width: 80%;
	 	margin: 0 auto;
	 }
	 .wirte-order td input{
	 	box-sizing: border-box;
	 	padding-left: 10px;
	 	font-size: 12px;
	 	width: 80%;

	 }
	 .look{
	 	background-color: #398DEE;
	 	color: #fff;
	 	font-size: 13px;
	 	border: none;
	 }
	 .wirte-btn{
	 	background-color: #F64B30;
	 	color: #fff;
	 	font-size: 13px;
	 	border: none;
	 }
	 .write-ok{
	 	background-color: #52C436;
	 	color: #fff;
	 	border: none;
	 	font-size: 13px;
	 }
	 .pay_status{
	 	width: 100%;
	 	text-align: center;
 		background-color: #fff;
 		border: none;
 	}
 	.order-manage .image select{
		width: 80%;
	    text-align: center;
	    box-sizing: border-box;
	    /* padding-left: 30px; */
	    outline: none;
	    border-radius: 4px;
	    font-size: 16px;
	    height: 30px;
	    border: 1px solid #D4D4DC;
	    border: none;
 	}
	 .wirte-order td .sele{
	 	width: 80%;
	 	text-align: center;
	 	box-sizing: border-box;
	 	padding-left: 30px;
	 	outline: none;
	 	border-radius: 4px;
	    font-size: 16px;
	    height: 30px;
	    border: 1px solid #D4D4DC;
	 }
	.banner_infor{
		margin-top: 10px;
		width:100%;
		border-top: 2px solid #D3D6DF;
		background-color: white;
		border-collapse: collapse;
		tr{
			td{
				width:260px;
				height:35px;
				text-align: center;
				border:1px solid #e8e8e8;
			}
		}	
		.wlmsg{
			input{
				box-sizing: border-box;
				padding-left:10px;
				width:60%;
				margin:3px 0;
				font-size: 12px;
			}
		}	
	}
</style>



