<template>
	<header class="shadow mheader">
		<h1>
			蜗创后台管理
		</h1>
		<div>
			<span class="exit"><a href="#">注销</a></span>
		</div>
	</header>
</template>

<script>
import ser from "../js/serverUrl";
	$(function(){
		$(".exit").click(function(){
			$.ajax({
				type:"POST",
				url :ser.url("doExit"),
				dataType: 'json',
				success:function(resp){
					window.localStorage.removeItem('adminMassge');
					window.location.href="/index/admin/login";
				}

			})
		})
	})
	export default {
	}
</script>

<style scoped lang="scss">
	.mheader{
		background-color: #0099CC;
		h1{
			padding: 10px 20px;
		    color: #F0FFFF;
		    display: inline-block;
		    font-size: 25px;
		    font-weight: 400;
		}
		div{
			color:#F0FFFF;
			float: right;
			margin:15px 45px;
			span{
				margin-left: 10px;
				a
				{
				 color:#F0FFFF;}	
			}
		}
	}
</style>



