export default {
	//顶部提示框
	alertTop:function (msg,time) {

		var alert_wrap = document.createElement('div');
		alert_wrap.setAttribute('id','J_alert_top');
		alert_wrap.setAttribute('style','position: fixed;z-index:9;top: 0px;transform:translate(0, -100%);width: 100%;padding: 10px 0;background-color: #000;opacity: .7;color: #fff;text-align: center;transition: all 800ms ease;word-wrap:break-word;');
		alert_wrap.innerHTML = msg;
		document.getElementById('J_alert_top')||document.body.appendChild(alert_wrap);

		setTimeout(function(){
			alert_wrap.style.transform = 'translate(0, 0)';
		},0.1);
		setTimeout(function(){
			alert_wrap.style.transform = 'translate(0, -100%)';
			alert_wrap.setAttribute('id','');
		},time||1500);
	},
	//单按钮提示框
	alert:function (msg) {
		var alert_wrap = document.createElement('div');
		alert_wrap.setAttribute('id','J_alert');
		alert_wrap.setAttribute('style','width: 300px; height: 130px;z-index:9; box-sizing:border-box; padding:20px 10px; background: #FFF ;box-shadow: 0px 0px 6px black ;border-radius: 3px;position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);color: #000;text-align: center;word-wrap:break-word;letter-spacing:2px;');
		alert_wrap.innerHTML = msg;
		document.getElementById('J_alert')||document.body.appendChild(alert_wrap);
		var alert_btn = document.createElement('button');
		alert_btn.innerHTML='确定';
		alert_btn.setAttribute('style','width:100%;height:35px;z-index:9;display:block;margin-top:30px;background:#000;color:#fff;border:0;border-radius:4px;')
		alert_wrap.append(alert_btn);
		alert_btn.onclick = function(){
			alert_wrap.remove();
		}
	},
	//这是啥
	alertS:function (msg,callback) {
		var _this=this;
		var alert_wrap = document.createElement('div');
		alert_wrap.setAttribute('id','J_alerts');
		alert_wrap.setAttribute('style','width: 300px; height: 130px;z-index:9; display:block; box-sizing:border-box; padding:20px 10px; background: #FFF ;box-shadow: 0px 0px 6px black ;border-radius: 3px;position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);color: #000;text-align: center;word-wrap:break-word;letter-spacing:2px;');
		alert_wrap.innerHTML = msg;
		document.getElementById('J_alerts')||document.body.appendChild(alert_wrap);
		var alert_btn1 = document.createElement('button');
		var alert_btn2 = document.createElement('button');
		var alert_btn_wrap = document.createElement('div');
		alert_btn1.innerHTML='取消';
		alert_btn2.innerHTML='确定';
		alert_btn1.setAttribute('style','width:30%;height:30px;z-index:9;display:inline-block;margin-top:30px;margin-left:10px;position:absolute;left:20px;background:#000;color:#fff;border:0;border-radius:4px;')
		alert_btn2.setAttribute('style','width:30%;height:30px;z-index:9;display:inline-block;margin-top:30px;margin-right:10px;position:absolute;right:20px;background:#000;color:#fff;border:0;border-radius:4px;')
		alert_wrap.append(alert_btn_wrap);
		alert_btn_wrap.setAttribute('style','width:100%;position:relative');
		alert_btn_wrap.append(alert_btn1)
		alert_btn_wrap.append(alert_btn2)
		alert_btn1.onclick = function(){
			alert_wrap.remove();
			_this.alertMsg("取消成功");
		}
		alert_btn2.onclick = function(){
			alert_wrap.remove();
			callback();
			
		}
	},
	//弹出提示框文字
	alertMsg:function (msg,time) {
		if (document.getElementById('J_alert_msg')) {
			return;
		}
		var alert_wrap = document.createElement('div');
		alert_wrap.setAttribute('id','J_alert_msg');
		alert_wrap.setAttribute('style', 'position: fixed; top: 50%; left: 50%; z-index:9; transform: translate(-50%,-50%);word-wrap: break-word;letter-spacing: 1px; text-align: justify;line-height: 1.5; background-color: rgba(0,0,0,.8);color: #fff;border-radius: 4px; padding: 20px; max-width: 300px; ')
		alert_wrap.innerHTML = msg;
		document.body.append(alert_wrap);
		setTimeout(function(){
			alert_wrap.remove();
		},time||1500);
	},
	//小于10的数字自动补零
	zeroFill:function (num) {
		return (num<10)?'0'+num:''+num;
	},
	//添加class
	addClass: function(e, className){
		var cn = e.className;
		if (cn.indexOf(className) >= 0) {
			return;
		}
		e.className = cn + ' ' + className;
	},
	//判断是否为以移动端
	isMobile: function(){
		return navigator.userAgent.toLowerCase().indexOf('mobile') >= 0;
	},
	//获取url上search的参数
	getParams: function(paramName){
		var params = {};
		var searchtxt = location.search.replace('?','');

		var paramsArr = searchtxt.split('&');

		for (var i = 0; i < paramsArr.length; i++) { 
			var s = paramsArr[i].split('=');
			console.log(s);
			params[s[0]] = s[1];
		}

		return paramName ? params[paramName] : params;
	},
	//获取长度 中文占两个字符
	getLen: function(str){
		if (str == undefined) return 0;
		if (typeof str != "string"){
			str += '';
		}
		return str.replace(/[^\x00-\xff]/g, "00").length;
	}

}
	


