<template>
	<section class="sale_pri">
		<table class="dex_tab">
		    <tr>
		    	<td colspan="6">
		    		<h2>商品特价表</h2>
		    	</td>
		    </tr>
			<tr>
				<td class="id">特价id</td>
				<td class="good_id" >商品id</td>
				<td class="good_name" >商品名称</td>
				<td class="" >商品价格</td>
				<td class="createtime" >创建时间</td>
				<td class="set" >操作</td>
			</tr>
			<tr v-for="data in dataList">
				<td class="id" >{{data.id}}</td>
				<td class="good_id" >{{data.good_id}}</td>
				<td class="name" >{{ data.name }}</td>
				<td class="price" ><input class="w-g-price" type="text" v-model="data.price" placeholder="修改价格"></td>
				<td class="createtime">{{data.createtime}}</td>
				<td class="set">
					<button class="write fin-write" @click="writeItem(data)">修改</button>
					<button class="del fin-delete" @click="deleteItem(data)">删除</button>
				</td>
			</tr>
			<tr>
				<td colspan="6" id="fenye"></td>
			</tr>
		</table>
		<footer>
			<input class="g_id" type="text" placeholder="请输入商品ID">
			<input class="g_price" type="text" placeholder="请输入商品价格">
			<button class="submit fin-search" @click="addItem()">添加特价</button>
		</footer>
	</section>
</template>

<script>
import ser from "../js/serverUrl";
	export default {
		data(){
			return {
				dataList:[],
				currentPage:'1',       //默认 当前第一页
				allPage:'',
				data11:{
					num:'10',
					currentPage:'1'
				}
			}
		},
		methods:{
					
			// 添加特价商品
			addItem:function(){
				var data1 = {
					good_id:$('.sale_pri footer .g_id').val(),
					price:$('.sale_pri footer .g_price').val()
				}
				var _this = this;
				$.ajax({
					type:"POST",
					url :ser.url("addSale"),
					data:data1,
					dataType: 'json',
					success:function(resp){
						if (resp.code == 200) {
							_this.util.alertMsg(resp.msg);
							_this.fajax();
						}else{
							_this.util.alertMsg(resp.msg);
						}
					}
				})
			},


			// 删除特价商品
			deleteItem:function(data){
				var data1={
					id:data.id
				}
				var _this = this;
				$.ajax({
					type:"POST",
					url :ser.url("delSale"),
					data:data1,
					dataType: 'json',
					success : function(resp){
						if (resp.code==200) {
							_this.util.alertMsg(resp.msg);
							_this.loadData();	
							_this.fajax();
						}else{
							_this.util.alertMsg(resp.msg);
						}
					}
				})
			},

			// 修改特价商品价格
			writeItem:function(data){
				var data1={
					id:data.id,
					price:data.price
				}
				var _this = this;
				$.ajax({
					type:"POST",
					url :ser.url("modifySale"),
					data:data1,
					dataType: 'json',
					success:function(resp){
						if (resp.code==200) {
							_this.util.alertMsg(resp.msg);
							_this.loadData();
						}else{
							_this.util.alertMsg(resp.msg);
							_this.loadData();
						}
					}
				})
			},
			loadData : function(){
				var _this=this
				var ul=document.createElement('ul');
				var li_first=document.createElement('li');
				var li_pre=document.createElement('li');
				var li_next=document.createElement('li');
				var li_last=document.createElement('li');
				var li_nowPage=document.createElement('li');
				var li_allPage=document.createElement('li');

				li_first.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer; color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_pre.setAttribute('style', 'background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_next.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				
				li_last.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')


				li_first.setAttribute('id','li_first')
				li_pre.setAttribute('id','li_pre')
				li_next.setAttribute('id','li_next')
				li_last.setAttribute('id','li_last')
				li_nowPage.setAttribute('id','li_nowPage')
				li_allPage.setAttribute('id','li_allPage')

				ul.appendChild(li_first);
				ul.appendChild(li_pre);
				ul.appendChild(li_next);
				ul.appendChild(li_last);
				ul.appendChild(li_nowPage);
				ul.appendChild(li_allPage);

				li_first.innerHTML='首页';
				li_pre.innerHTML  ='上一页';
				li_next.innerHTML ='下一页';				
				li_last.innerHTML ='尾页';
				
				function dd(a){
					a.onmouseover=function(){
						this.style.background='#333';
						this.style.color='#fff';
					}
					a.onmouseout=function(){
						this.style.background='#ddd';
						this.style.color='#000';
					}
				}
				dd(li_first)
				dd(li_next)
				dd(li_pre)
				dd(li_last)
				document.getElementById('fenye').innerHTML = '';
				document.getElementById('fenye').appendChild(ul);
				this.fajax();
			},
			fajax:function(){
				var _this=this
				$.ajax({
					type: "POST",
					data:_this.data11,
					url :ser.url("getSaleList"),
					dataType : "json",
					success : function(resp){
						if(resp.acount){
			        		_this.allPage=Math.ceil(resp.acount/_this.data11.num);
			        	}
			        	_this.doclick()
							_this.dataList=resp.data;
						li_nowPage.innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';	
					}
				})

			},
			doclick:function(){
				var _this = this
				li_first.onclick=function(){
					if(_this.data11.currentPage!=1){
						_this.data11.currentPage=1;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';
				}

				li_pre.onclick=function(){		
					if(_this.data11.currentPage<=_this.allPage&&_this.data11.currentPage>1)
					{
						_this.data11.currentPage--;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
				li_next.onclick=function(){
					if(_this.data11.currentPage<_this.allPage&&_this.data11.currentPage>=1)
					{
						_this.data11.currentPage++;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}

				li_last.onclick=function(){
					if(_this.data11.currentPage!=_this.allPage){
						_this.data11.currentPage=_this.allPage;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
			},
		},
		mounted(){
			console.log(ser)
			this.loadData();
		}
	}
</script>

<style scoped lang="scss">
	.sale_pri{
		position: relative;
	}
	.price{
		position:relative;
		.w-g-price{
			position:absolute;
			left: 50%;
			text-align: center;
			top: 50%;
			transform:translate(-50%,-50%);
			width:35%;
			height: 25px;
			/* border:none; */
			box-sizing:border-box;
			padding: 0 5px;
			font-size:12px;
		}
	}
	
    .write{
    	width:40%;
    	height:28px;
    	margin:5px 5px;
    	background:#F64B30;
    	color:#ffffff;
    	border:none;
    }
    .del{
    	width: 40%;
    	height: 28px;
    	margin: 0 5px;
    	background:#E21918;
    	color:#ffffff;
    	border:none;
    }
    h2{
    	padding : 5px 0;
    }
	table{
		width: 100%;
		border-collapse:collapse;
		background-color:#ffffff;

		tr:nth-child(2) td{
			padding: 8px 0;
			font-weight: 600;
		}
		tr{
			td{
				text-align: center;
				border:1px solid #e8e8e8;
			}
		}
	}
	footer{
		position: fixed;
		bottom:30px;
		width:50%;
		height:80px;
		line-height:80px;
		text-align:center;
		background:#ffffff;
		box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.50);
		border-radius: 5px;
		input::-webkit-input-placeholder{color: #4D4D4B;}
		input:focus::-webkit-input-placeholder{color: #eeeeee;}
		.g_id , .g_price{
			width:30%;
			height:30px;
			margin-right:10px;
			font-size:12px;

			box-sizing: border-box;
			padding-left: 10px;
			vertical-align: middle;
		}
		.submit{
			width:100px;
			height:30px;
			background:#4C8CF5;
			color:#ffffff;
			font-size:14px;
			vertical-align: middle;
		}

	}
</style>



