import Vue from 'vue'
import Router from 'vue-router'
import manager from '../components/manager'
import user from '../components/user'
import mainpageSet from '../components/mainpageSet'
import mainpageBanner from '../components/mainpageBanner'
import mainpageProductType from '../components/mainpageProductType'
import mainpageBargainPrice from '../components/mainpageBargainPrice'
import mainpageProducts from '../components/mainpageProducts'
import productDetails from '../components/productDetails'
import productedit from '../components/productedit'
import productadd from '../components/productadd'
import ordering from '../components/ordering'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/'
    },{
      path: '/manager',
      component: manager
    },{
      path: '/user',
      component: user
    },{
      path: '/mainpage/set',
      component: mainpageSet
    },{
      path: '/mainpage/banner',
      component: mainpageBanner
    },{
      path: '/mainpage/type',
      component: mainpageProductType
    },{
      path: '/mainpage/bargainPrice',
      component : mainpageBargainPrice
    },{
      path: '/mainPage/products',
      component: mainpageProducts
    },{
      path: '/productDetails',
      component: productDetails
    },{
      path: '/productedit',
      component: productedit
    },{
      path: '/ordering',
      component: ordering
    },{
      path: '/productadd',
      component: productadd
    }
  ]
})



