<template>
	<section class="pro_det">
		<!-- 商品主页 -->
		<div >
			<button class="addproduct fin-search" @click="addproduct()">添加商品</button>
			<div class="search-right">
	    		<input type="text" placeholder="输入要查找的商品名称" class="search_text">
		    	<button class="fin-search" @click="searchClick">搜索</button>
		    	<button class="fin-all-show" @click="loadData">显示全部商品</button>
	    	</div>
		</div>
		<table class="dex_tab"> 
			
			<tr>
				<td class="id" width="100">id</td>
				<td class="name" >商品名称</td>
				<td class="images" >商品图片</td>
				<td class="price" >价格</td>
				<td class="createtime" >创建时间</td>
				<td class="set" >操作</td>
			</tr>
			<tr v-for="(data,index) in dataList">
				<td class="id" >{{data.id}}</td>
				<td class="name" >{{data.name}}</td>
				<td class="images" width="100"  style="overflow:hidden;">
					<img :src='data.images[0]' @click="tobig(data)">
					<!-- <img :src="imgSrcList[index]" @click="tobig(data)"> -->
				</td>
				<td class="price" >{{data.price}}</td>
				<td class="createtime" >{{data.createtime}}</td>
				<td class="set" width="150"> 
					<button class="change wirte-btn" @click="changeProduct(data)">修改</button>
					<button class="delete fin-delete" @click="deleteGoods(data)">删除</button>
				</td>
			</tr>
			<tr>
				<td colspan="6" id="fenye"></td>
			</tr>		
		</table>

		
        <div class="big" style="display:none;">
            <button class="close alert-close" style="float: right;" v-on:click="close">X</button>
            <img src="" alt="" class="bigimg">
		</div>
	</section>
</template>

<script>
import ser from "../js/serverUrl";
	var vm = {
		data(){
			return {
				isAddAttrInput:false,
				isAttrValueInput:false,
				isAttrValueList:false,
				attrList:[],
				newAttrVal:"",
				dataList:[],
				imageSrcList:[],
				stockList:{},
				imgSrcList:[],
				currentPage:'1',       //默认 当前第一页
				allPage:'',
				data11:{
					num:'10',
					currentPage:'1'
				}
			}
		},
		methods:{
			backClick:function(){
				$('.pro_det .all_tab').attr('style','display:none');
				$('.pro_det .change_tab').attr('style','display:none');
				$('.pro_det .add_tab').attr('style','display:none');
				$('.pro_det .dex_tab').attr('style','display:');
				_this.loadData();
			},
			searchClick:function(){
				var _this = this
				var txt = $('.pro_det .search_text').val();
				var idd = {
					keyword:txt
				}
				$.ajax({
					type:'POST',
					url :ser.url("getGoodsByKeyWord"),
					data:idd,
					dataType: 'json',
					success : function(resp){
						_this.dataList=resp.data;
					}
				})
			},
			addproduct:function(){
				this.$router.push({ path: 'productadd', query: {}})
			},
			changeProduct:function(data){
				/*console.log(data)*/
				this.$router.push({ path: 'productedit', query: { 
					id:data.id
				}})
			},
			
			deleteGoods:function(data){
				var _this = this;
				_this.util.alertS('你确认要删除吗?',callback)
				function callback(){
					var arr ={
						id:data.id
					}
					$.ajax({
						type:'POST',
						url :ser.url("delGoods"),
						data:arr,
						dataType: 'json',
						success:function(resp){
							if(resp.data==1){
								_this.loadData();
								_this.util.alertMsg(resp.msg)
							}  
						}
					})
				}	
			},
			uploadImgUrl:function(event){
				var formdata = new FormData();
				formdata.append("imgFile", event.currentTarget.files);
				var _this=this;
				$.ajax({
					type:'post',
					url :ser.url("doUploadImgGoods"),
					data : formdata, 
					dataType: 'json',
					cache : false, 
					contentType : false, 
					processData : false, 
					config: {
						headers: {'Content-Type': 'multipart/form-data'}
					},
					success:function(resp){
						_this.imageSrcList.push(resp.data);
	              }
	          })
				return false;  
			},
			loadData:function () {

				var _this=this
				var ul=document.createElement('ul');
				var li_first=document.createElement('li');
				var li_pre=document.createElement('li');
				var li_next=document.createElement('li');
				var li_last=document.createElement('li');
				var li_nowPage=document.createElement('li');
				var li_allPage=document.createElement('li');

				li_first.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer; color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_pre.setAttribute('style', 'background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				li_next.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')
				
				li_last.setAttribute('style','background:#ddd;margin:0px 10px; cursor:pointer;   color:#000;width:80px;height:30px;text-align:center;line-height:30px;float:left;list-style:none')


				li_first.setAttribute('id','li_first')
				li_pre.setAttribute('id','li_pre')
				li_next.setAttribute('id','li_next')
				li_last.setAttribute('id','li_last')
				li_nowPage.setAttribute('id','li_nowPage')
				li_allPage.setAttribute('id','li_allPage')

				ul.appendChild(li_first);
				ul.appendChild(li_pre);
				ul.appendChild(li_next);
				ul.appendChild(li_last);
				ul.appendChild(li_nowPage);
				ul.appendChild(li_allPage);

				li_first.innerHTML='首页';
				li_pre.innerHTML  ='上一页';
				li_next.innerHTML ='下一页';				
				li_last.innerHTML ='尾页';
				
				function dd(a){
					a.onmouseover=function(){
						this.style.background='#333';
						this.style.color='#fff';
					}
					a.onmouseout=function(){
						this.style.background='#ddd';
						this.style.color='#000';
					}
				}
				dd(li_first)
				dd(li_next)
				dd(li_pre)
				dd(li_last)
				
				document.getElementById('fenye').innerHTML='';
				document.getElementById('fenye').appendChild(ul);
				this.fajax();
			},
			fajax:function(){
				var _this=this
				$.ajax({
					type: "POST",
					url :ser.url("getGoodsList"),
					data:_this.data11,
					dataType: 'json',
					success : function(resp){
						if(resp.acount){
			        		_this.allPage=Math.ceil(resp.acount/_this.data11.num);
			        	}
			        	_this.doclick()
						_this.dataList=resp.data;
						li_nowPage.innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';	
					}
				})
			},
			doclick:function(){
				var _this = this
				li_first.onclick=function(){
					if(_this.data11.currentPage!=1){
						_this.fajax();	
						_this.data11.currentPage=1;
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页';
					console.log(_this.data11.currentPage)
				}

				li_pre.onclick=function(){		
					if(_this.data11.currentPage<=_this.allPage&&_this.data11.currentPage>1)
					{
						_this.data11.currentPage--;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
				li_next.onclick=function(){
					if(_this.data11.currentPage<_this.allPage&&_this.data11.currentPage>=1)
					{
						_this.data11.currentPage++;
						_this.fajax();	
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}

				li_last.onclick=function(){
					if(_this.data11.currentPage!=_this.allPage){
						_this.fajax();	
						_this.data11.currentPage=_this.allPage;
					}
					document.getElementById('li_nowPage').innerHTML ='当前第 '+_this.data11.currentPage+' 页 '+' &nbsp;'+' 共 '+_this.allPage+' 页 ';
					console.log(_this.data11.currentPage)
				}
			},
			tobig:function(data){
				var cururl = data.images[0];
				$('.big').attr('style','display:');
				$('.bigimg').attr('src', '' + cururl+'')
			},
			close:function(){
				$('.big').attr('style','display:none');
			}

		},
		mounted(){
			this.loadData();
		}
	};
	export default vm;
</script>

<style scoped lang="scss">
    *{
		font-family: '微软雅黑';
	}
	input[type='text']{
		padding-left:5px;
	}
	.search-right{
		float: right;
	}
	.search_text{
		width:200px;
	}
	table{
		border:1px solid #ddd;
		background-color:#fff;
	}
	button {
	    padding: 1px 10px;
	    text-align: center;
	    border-radius: 3px;
	    height: 28px;
	    cursor: pointer;
	    margin: 3px 0;
	    font-size: 13px;
	    
	}
	.dex_tab{
		width: 100%;
		border-collapse:collapse;
		border-top: 2px solid #D3D6DF;
		margin-top:10px;
		img{
			width:100px;
			max-height:100px;
		}
		tr{
			td{
				text-align: center;
				text-indent: 0.2em;
				width:260px;
				height:35px;
				border:1px solid #e8e8e8;
				width:260px;
				height:35px;

			}
			td:nth-child(2){
				text-indent: 0;
			}

		}
		
		.allcontent{
			display:block;
			width:80px;
		}
		.change{
			background-color: #F64B30;
		 	color: #fff;
		 	font-size: 13px;
		 	border: none;
		 	width:27%;
		}
	}
	.addproduct{
		width:80px;
		
		right:200px;
	}
	.big{
		width:60%;
		height:60%;
		top:0;
		bottom:0;
		right:0;
		left:0;
		margin: auto;
		position: fixed;
		overflow:auto;
		background-color: rgba(0, 0, 0, .7);
		.bigimg{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}
	

</style>


