<template>
	<section class="mainBanner">
	    <!-- 分类主页 -->
	    <div class="search">
	        <button class="add_class fin-search" @click="addData">添加分类</button>
	    	<div style="clear: both"></div>
	    </div>
		<table class="class_tab">
			<tr>
				<td class="id">分类id</td>
				<td class="image" >分类图片</td>
				<td class="text" >分类名称</td>
				<td class="sort" >排序</td>
				<td class="createtime" >创建时间</td>
				<td class="set" width="100">操作</td>
			</tr>
			<tr v-for="data in dataList">
				<td class="id" ref="td">{{data.id}}	</td>
				<td class="image" >
					<img :src="data.image" @click="tobig(data)">		
					<input type="file" class="img_file" v-show="data.isShowInput" @change="imageChange($event,data)" >					
				</td>
				<td class="text" >
				   {{data.text}}
				   <input type="text" v-model="data.text" v-show="data.isShowInput">
				</td>
				<td class="sort" >
				    {{data.sort}}
				    <input type="text" v-model="data.sort" v-show="data.isShowInput">
				</td>
				<td class="createtime" >{{data.createtime}}</td>
				<td class="set">
					<button class="editor fin-write" @click="editorClick(data)">修改</button>
					<button class="fin-done" @click="editorokClick(data)">完成</button>
					<button class="fin-delete" @click="deleteClick(data)">删除</button>
				</td>
			</tr>
			<div class="big" style="display:none;">
	            <button class="close alert-close" style="float: right;" v-on:click="close">X</button>
	            <img src="" alt="" class="bigimg">
		    </div>		
		</table>

		<!-- 添加新分类页 -->
		<table class="add_tab" style="display:none">
			<tr>
				<td colspan="7">
					<h2>添加分类</h2>
				</td>
			</tr>
			<tr>
		     	<td class="text" >分类名称</td>
		     	<td>
		     		<input type="text" class="adtext">
		     	</td>
			</tr>
		    <tr>
		     	<td class="image" >分类图片</td>
		     	<td valign="middle">
		     		<input type="file" class="adimg" @change="addimg_load($event)">
		     		<img class="gg" src="" style="width:100px;height:100px" >
		     	</td>
			</tr>	
			<tr>
		     	<td class="sort" >排序</td>	
		     	<td>
		     		<input text=""class="adsort">
		     	</td>
			</tr>
		    <tr>
		    	<td colspan="2">
		    		<button class="cl_back fin-search" @click="backdex">返回</button>
		    		<button class="cl_back fin-search" @click="addDataok">提交</button>
		    	</td>
		    </tr>	     
		</table>
		
	</section>
</template>

<script>
import ser from "../js/serverUrl";
	export default {
		data(){
			return{
				dataList:[],
				images:"",
				images1:""
			}
		},
		methods:{
					
			/*返回按钮*/
			backdex:function(){
				$('.add_tab').attr('style','display:none');
				$('.class_tab').attr('style','display:');
				$('.mainBanner .search').attr('style','display:');
			},

			/*搜索*/
			searchClick:function(){
				var _this = this
				var txt = $('.mainBanner .search_text').val();
				var idd = {
					keyword:txt
				}
				$.ajax({
					type:'POST',
					url :ser.url("getClassifyByKeyWord"),
					data:idd,
					dataType: 'json',
					success:function(resp){
						_this.dataList=resp.data;
					}
				})
			},

			/*删除单个分类*/
			deleteClick:function(data){

				if(confirm('你确认要删除吗？')){
					var _this = this;
	    		    var clid = {
	    		    	id:data.id
	    		    }

	    		    $.ajax({
						type: "POST",
						data:clid,
						url :ser.url("delClassify"),
						dataType: 'json',
						success:function(resp){
							_this.util.alertMsg(resp.msg);
							if(resp.data==1){
								_this.loadData();
							}	
						}
					})
	    		}
			},

			/*添加分类跳转按钮*/
			addData:function(){
				$('.add_tab input').val("");
				$('.mainBanner .add_tab img')[0].src="";
				$('.mainBanner .class_tab').attr('style','display:none');
				$('.mainBanner .search').attr('style','display:none');
    		    $('.mainBanner .add_tab').attr('style','display:'); 
			},

			/*添加页图片自上传*/
			addimg_load:function(event){
				var formdata = new FormData();
				var v_this = $(event.currentTarget); 
				var fileObj = v_this.get(0).files; 
				formdata.append("imgFile", fileObj[0]); 
				jQuery.ajax({
			        type:'post',
			        url :ser.url("doUploadImgClassify"),
					data : formdata, 
		        	cache : false, 
					contentType : false, 
					processData : false, 
					dataType : "json", 
					config: {
						 headers: {'Content-Type': 'multipart/form-data'}
					},
			        success:function(res){
			        	$('.mainBanner .add_tab img')[0].src=res.data
			        }
			    })
			},

			/*添加分类完成按钮 */
			addDataok:function(){
			    if(confirm('确认添加分类？')){
				    var _this = this;
	    		    var arr={	
						image:$('.mainBanner .add_tab img')[0].src,
						text:$(".adtext").val(),
						sort:$(".adsort").val(),
					};
					var im=$('.mainBanner .add_tab .adimg').val()
					console.log(im)
					var reg =  new RegExp("\\s+");
					if(im !== "" && !reg.test(arr.text) && !reg.test(arr.sort) &&  arr.text != "" && arr.sort !== ""){
						$.ajax({
							type:"POST",
							data:arr,
							url :ser.url("addClassify"),
							dataType: 'json',
							success:function(resp){
								if(resp.data==1){
									_this.util.alertMsg(resp.msg);
									_this.loadData();
								}else{
									_this.util.alertMsg(resp.msg);
								}
								
							}
					    })
					    $('.add_tab').attr('style','display:none')
						$('.class_tab').attr('style','display:')
						$('.mainBanner .search').attr('style','display:');
					}else{
						_this.util.alertMsg("内容不能为空或有空格");
					}
			    }	    
    		    
			},

			/*主页修改图片自上传*/
			imageChange:function(event,data){
				var formdata = new FormData();
				var v_this = $(event.currentTarget); 
				var fileObj = v_this.get(0).files; 
				formdata.append("imgFile", fileObj[0]); 
				jQuery.ajax({
			        type:'post',
			        url :ser.url("doUploadImgClassify"),
					data : formdata, 
		        	cache : false, 
					contentType : false, 
					processData : false, 
					dataType : "json", 
					config: {
						 headers: {'Content-Type': 'multipart/form-data'}
					},
			        success:function(res){
			            data.image=res.data;
			        }
			    })
			},

			/*修改单个分类完成按钮*/
			editorokClick:function(data){
				var _this=this
				if(confirm('是否确认修改？')){
					var txt = $('.text input').val()
					var sor = $('.sort input').val()
					var img = data.image
					if(sor !== "" && txt !== "" && img !== ""){
						data.isShowInput = false;
						var arr={
							id:data.id,
							image:data.image,
							text:data.text,
							sort:data.sort
						}
							$.ajax({
								type: "POST",
								url :ser.url("modifyClassify"),
								data:arr,
								dataType: 'json',
								success : function(resp){
									if(resp.data==1){
										_this.util.alertMsg(resp.msg);
										_this.loadData();
									}else{
									 	_this.util.alertMsg(resp.msg);
									}
								}
							})
					}else{
						_this.util.alertMsg("内容不能为空");
					}
					
				}
				
			},

			/*修改单个分类*/
			editorClick:function(data){
				data.isShowInput = true;
			},

			/*分类主页*/
			loadData:function(){
				var _this = this;
				$.ajax({
					type:'POST',
					url :ser.url("getClassifyList"),
					dataType: 'json',
					success:function(resp){
						for(var i=0;i<resp.data.length;i++){
							resp.data[i].isShowInput = false;
						}
						_this.dataList=resp.data;
					}
				})
			},
			tobig:function(data){
				var cururl = data.image;
				$('.big').attr('style','display:');
				$('.bigimg').attr('src', '' + cururl+'')
			},
			close:function(){
				$('.big').attr('style','display:none');
			}
		},
		
		mounted(){
			this.loadData();
		}
	}
</script>

<style scoped lang="scss">
    .mainBanner{
		.search{
			width:100%;
			margin-bottom:3px;
			.search-right{
				float: right;
			}
			.add_class{
				width: 100px;
			}
			h2{
				text-align:center;
			}
			button{
				padding:2px 5px;
			}
    		input{
    			display:inline-block;
    			padding-left:5px;
    		}
    		div:nth-child(2){
    			display: inline-block;
    		}
    		div:nth-child(3){
    			
    			float:right;
    			display: inline-block;
    		}
    	}
    	.class_tab{
    		width:100%;
			margin-top: 10px;
			border-top: 2px solid #D3D6DF;
			background-color: white;
			border-collapse: collapse;
			input[type='text']{
				width:100px;
				position:relative;
				top:35px;
			}
			input{
				width:200px;
				display:block;
				margin:0 auto;
				padding:5px;
			}
			.set{
				width: 300px;
			}
		
			button{
				/*background-color:#fff;*/
				padding:2px 5px;
			}
			tr{
				td{
					text-indent: 0.2em;
					width:260px;
					height:35px;
					border:1px solid #e8e8e8;
					text-align:center;
					img{
						width:100px;
						max-height:100px;
					}
				}
			}
			.alert-close{
			    font-size: 25px;
			    background-color: rgba(0,0,0,.5);
			    color: #fff;
			}
			.big{
				width:40%;
				height:60%;
				top:0;
				bottom:0;
				right:0;
				left:0;
				margin: auto;
				background-color: rgba(0, 0, 0, .7);
				position: fixed;
				overflow:auto;
				.bigimg{
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
				}
			}
			
	    }
		.add_tab{
			width:100%;
			margin-top: 10px;		
			background-color: white;
			border-collapse: collapse;
			input{
				padding-left:5px;
			}
			button{
				background-color:#fff;
				width:80px;
			}
			tr{
				td{
					text-indent: 0.2em;
					width:260px;
					height:35px;
					border:1px solid #C1C4CB;
				}
				td:nth-child(2){
					input{
						width:500px;
					}
					text-indent: 0;
					width:500px;
				}
			}
			tr:nth-child(5){
				text-align:center;

			}
			
		}
    }
	
</style>



