<template>
	<section >
		<div class="out">
			<button class="add_btn fin-search" v-on:click="add">添加幻灯片</button>
			<table class="banner_infor">
				<tr>
					<td class="id" >id</td>
					<td class="image" >图片</td>
					<td class="type" >类型</td>
					<td class="text" >描述</td>
					<td class="link" >链接</td>
					<td class="createtime" >创建时间</td>
					<td class="sort" >排序</td>
					<td class="set" >操作</td>
				</tr>
				<tr v-for="data in dataList">
					<td class="id" width="20">{{data.id}}</td>
					<td class="image" width="100" v-on:click="tobig(data)">
						<img :src="data.image" width="100%">
					</td>
					<td class="type" width="20">{{data.type}}</td>
					<td class="text" width="200">{{data.text}}</td>
					<td class="link" width="100">{{data.link}}</td>
					<td class="createtime" width="100">{{data.createtime}}</td>
					<td class="sort">{{data.sort}}</td>
					<td class="set" style="width:15%;">
						<button v-on:click="change(data)" class="fin-change fin-write">修改</button>
						<button v-on:click="dee(data)" class="fin-delete">删除</button>	
					</td>
				</tr>
			</table>
		</div>
		<div class="in" style="display:none">
			<table class="in-infor">
				<tr>
					<td>
						<span>幻灯片类型:</span>
						<select id="hdcat" v-model="bannerClassify">
							<option  v-model="bannerClassify" v-for="item in bannerClassifyList">{{item.value}}</option>
						</select>
					</td>
				</tr>
				<tr>
					<td><span>图片:</span><input type="file" class="hdimg"></td>
				</tr>
				<tr>
					<td><span>文字介绍:</span><input type="text" class="hdconst"></td>
				</tr>
				<tr>
					<td><span>图片链接:</span><input type="text" class="imglink"></td>
				</tr>
				<tr>
					<td><span>排序:</span><input type="text" class="hdsort"></td>
				</tr>
				<button class="in_add fin-done" v-on:click="add_in">完成</button>
			</table>
		</div>
		<div class="big" style="display:none">
			<button class="close alert-close" style="float: right;" v-on:click="close">X</button>
			<img src="" alt="" class="bigimg">
		</div>
	</section>

</template>
<script>
import ser from "../js/serverUrl";
	export default {
		data:function(){
			return {
				cururl:"",
				dataList:[],
				images:"",
				curChangeId:null,
				bannerClassifyList:[
					{value:'首页顶部幻灯片'},
					{value:'首页中部幻灯片'},
					{value:'首页底部幻灯片'}
				],
				bannerClassify:'首页顶部幻灯片'
			}
		},
		methods:{
			//获取数据加载到页面
			loadData : function(){
				var _this=this;
				$.ajax({
					type: "POST",
					url :ser.url("getSliderList"), 
					dataType: 'json',
					success:function(resp){
				        _this.dataList=resp.data;
					}	
				})
			},
			add:function(){
    			$('.out').attr('style','display:none');
    			$('.in').attr('style','display:');
			},
			//图片自动上传
			img_load:function(){
				var _this = this;
				$(".hdimg").change(function(){
					var formdata = new FormData();
					var v_this = $(this); 
					var fileObj = v_this.get(0).files; 
					formdata.append("imgFile", fileObj[0]); 
					jQuery.ajax({
				        type:'post',
				        url :ser.url("doUploadImgSlide"),
						data : formdata, 
			        	cache : false, 
						contentType : false, 
						processData : false, 
						dataType : "json", 
						config: {
							 headers: {'Content-Type': 'multipart/form-data'}
						},
				        success:function(res){
				        	if (typeof res == 'string'){
				        		res = JSON.parse(res);
				        	}
				        	_this.util.alertMsg('图片上传成功');
				            _this.images=res.data;
				        }
				    })
				 }); 
			},
			//添加上传数据
			add_in:function(data){
				var _this=this
				if(this.curChangeId){
					var arr={
						id:this.curChangeId,
						type:_this.bannerClassify,
						image:this.images,
						text:$(".hdconst").val(),
						link:$(".imglink").val(),
						sort:$(".hdsort").val(),
					}
					$.ajax({
						type:'POST',
						url :ser.url("modifySlider"),
						data:arr,
						dataType: 'json',
						success:function(resp){
							 if(resp.msg =="修改成功"){ 
							 	_this.util.alertMsg(resp.msg);
							 	_this.loadData();
							}
						}
					});
					$('.hdimg').val('');
					_this.bannerClassify = '';
    				$(".hdconst").val('');
    				$(".imglink").val('');
					$(".hdsort").val('');
					$('.in').attr('style','display:none');
    				$('.out').attr('style','display:');
    				

    				this.curChangeId = null;
				}
				else{
					var arr={
					type:_this.bannerClassify,
					image:this.images,
					text:$(".hdconst").val(),
					link:$(".imglink").val(),
					sort:$(".hdsort").val(),
					}
					$.ajax({
						type: "POST",
						data:arr,
						url :ser.url("addSlider"),
						dataType: 'json',
						success:function(resp){
							if(resp.data==1){
								_this.util.alertMsg(resp.msg);
								_this.loadData();
							}						
						}
					});
					$('.hdimg').val('');
					_this.bannerClassify = '';
    				$(".hdconst").val('');
    				$(".imglink").val('');
					$(".hdsort").val('');
	    			$('.in').attr('style','display:none');
	    			$('.out').attr('style','display:');
					}
				},
			dee:function(data){
				var _this=this
				_this.util.alertS("确认or取消",aa);
				function aa(){
					var arr={
						id:data.id,
					}
					console.log(data.id);
					$.ajax({
						type:'POST',
						url :ser.url("delSlider"),
						data:arr,
						dataType: 'json',
						success:function(resp){
							if(resp.msg == "删除成功"){
								_this.util.alertMsg(resp.msg);
								_this.loadData();
							}
						}
					});
				}
			},
			//新改修改幻灯片
			change:function(data){
				var _this = this;
				$('.out').attr('style','display:none');
    			$('.in').attr('style','display:');
    			this.curChangeId = data.id;
    			_this.bannerClassify = data.type;
    			$(".hdconst").val(data.text);
    			$(".imglink").val(data.link);
    			$(".hdsort").val(data.sort);
			},
			tobig:function(data){
				var cururl = data.image;
				$('.big').attr('style','display:');
				$('.bigimg').attr('src', '' + cururl+'') 
			},
			close:function(){
				$('.big').attr('style','display:none');
			}
			
	    },

	    mounted(){
			this.loadData();
			this.img_load();
		}
	}
</script>

<style scoped lang="scss">
	.banner_infor{
		margin-top: 10px;
		width:100%;
		border-top: 2px solid #D3D6DF;
		background-color: white;
		border-collapse: collapse;
		tr{
			td{
				width:260px;
				height:35px;
				border:1px solid #e8e8e8;
			}
			td:nth-child(2){
				cursor: pointer;
			}
			td:nth-child(3){
				text-indent: 0;
			}
		}		
	}
	.inp{
		text-indent: 5px;
		margin-right: 10px;
	}
	.add_btn{
		width:100px;
	}
	.in-infor{
		margin-top: 10px;
		width:100%;
		border-top: 2px solid #D3D6DF;
		background-color: white;
		border-collapse: collapse;
		tr{
			td{
				height: 40px;
				line-height: 40px;
				border:1px solid #e8e8e8;
				padding-left: 10px;
				padding-right: 160px;
				input{
					width:62%;
					float: right;
					margin-top: 4px;
				}
				select{
					width: 62%;
					height:30px;
					outline:none;
					float: right;
					margin-top:4px;
					border:solid 1px #d4d4dc;
					border-radius:6px;
					option{
						display:inline-block;
						height:30px;
						padding: 5px 0;
					}
				}
			}
		}
	}
	.in_add{
		letter-spacing:8px;
		padding:1px 4px 1px 12px;
		width:70px;
		margin:10px 600px;
	}
	.big{
		/* width:65%; */
		/* height:60%; */
		position: fixed;
		top:0;
		bottom:0;
		right:0;
		left:0;
		margin: auto;
		overflow: auto;
		background-color: rgba(0, 0, 0, .8);
		.bigimg{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}
	.bigimg{

	}
	.img{
		cursor: pointer;
	}

</style>



